<template>
  <div :style="'height:' + containerHeight+'px; overflow-y:scroll;' ">
    <!--<Row>-->
      <!--<i-col span="12" style="padding-right: 16px;">-->
        <!--<Card :bordered="false" dis-hover class="member-card cursor-hand">-->
          <!--<div style="font-weight: 500" slot="title">-->
            <!--<Icon type="ios-stats"/>-->
            <!--执行频率-->
          <!--</div>-->
          <!--<div id="unclosed_issue_column" class="chart-trend"></div>å-->
        <!--</Card>-->
      <!--</i-col>-->
      <!--<i-col span="12">-->
        <!--<Card :bordered="false" dis-hover class="member-card cursor-hand">-->
          <!--<div style="font-weight: 500" slot="title">-->
            <!--<Icon type="ios-stats"/>-->
            <!--通过率-->
          <!--</div>-->
          <!--<div id="unclosed_issue_column2" class="chart-trend"></div>å-->
        <!--</Card>-->
      <!--</i-col>-->
    <!--</Row>-->
    <Card :bordered="false" dis-hover class="member-card cursor-hand">
      <div style="font-weight: 500;" slot="title">
        <Button @click="exportTestResult" shape="circle" icon="md-log-out" size="small" :loading="exporting">导出结果</Button>
      </div>
      <Row type="flex" justify="space-between">
        <Col span="6" style="padding: 20px; color: #00b0ff;">
        <Card :padding="20" style="height: 100px;" dis-hover>
          <div class="summary-number-title">Total:</div>
          <div class="summary-number-content">
            <span style="color: inherit" @click="filterCaseResult(0)">
              {{ testResult.Total }}
            </span>
          </div>
        </Card>
        </Col>
        <Col span="6"  style="padding: 20px;color: forestgreen;" >
        <Card :padding="20" style="height: 100px;" dis-hover>
          <div class="summary-number-title">Pass:</div>
          <div class="summary-number-content">
           <span style="color: inherit" @click="filterCaseResult(3)">
              {{ testResult.Pass }}
            </span>
          </div>
        </Card>
        </Col>
        <Col span="6"  style="padding: 20px;color:darkred;">
        <Card :padding="20" style="height: 100px;" dis-hover>

          <div class="summary-number-title">Fail:</div>
          <div class="summary-number-content">
            <span style="color: inherit" @click="filterCaseResult(2)">
              {{ testResult.Fail }}
            </span>
          </div>
        </Card>
        </Col>
        <Col span="6"  style="padding: 20px;color:orange;">
        <Card :padding="20" style="height: 100px;" dis-hover>

          <div class="summary-number-title">Aborted:</div>
          <div class="summary-number-content">
           <span style="color: inherit" @click="filterCaseResult(1)">
              {{ testResult.Aborted }}
            </span>
          </div>
        </Card>
        </Col>
      </Row>
    </Card>
    <Table border :columns="columns1" :data="caseResults.results" stripe :loading="loadingCaseResult"></Table>
    <div style="margin: 10px;overflow: hidden">
      <div style="float: right;">
        <Page :total="caseResults.count" :current="1" @on-change="changePage"></Page>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import labelEditorInput from '../../../components/common/LabelEditor-Input.vue'
import ICol from '../../../../node_modules/iview/src/components/grid/col.vue'
import ISwitch from '../../../../node_modules/iview/src/components/switch/switch.vue'
import HighCharts from 'highcharts'

export default {
  name: 'TestResultDetail',
  props: ['testResultID'],
  data () {
    return {
      testResult: {},
      caseResults: [],
      exporting: false,
      columns1: [
        {
          title: 'ID',
          key: 'id',
          width: 100,
          sortable: true,
        },
        {
          title: '描述',
          key: 'CaseDesc'
        },
        {
          title: '名称',
          key: 'TestCaseName'
        },
        {
          title: '结果',
          key: 'ResultFormat',
          width: 150,
          sortable: true,
          render: (h, params) => {
            const row = params.row;
            const color = row.Result === 2 ? 'error' : row.Result === 3 ? 'success' : 'error'

            return h('Tag', {
              props: {
                type: 'dot',
                color: color,
                size: 'small'
              }
            },row.ResultFormat)
          }
        },
        {
          title: '异常信息',
          key: 'Exception'
        }
      ],
      id: 'testRunSummary',
      loadingCaseResult: false
    }
  },
  computed: {
    ...mapGetters(['appBodyHeight', 'userInfo']),

    containerHeight: function () {
      return this.appBodyHeight
    }

  },

  methods: {

    getTestResult: function (groupID) {
      this.$axios.get('/api/ci/auto_testing_result/' + groupID +'/').then(response => {
        this.testResult= response.data.result

      }, response => {

      })
    },

    getCaseResults: function (groupID,resultType) {
      let resultFilter = ''
      if (resultType !==0){
        resultFilter = '&Result=' + resultType
      }
      this.loadingCaseResult = true
      this.$axios.get('/api/ci/auto_case_results?TaskResultID='+ groupID + resultFilter).then(response => {
        this.caseResults = response.data.result
        this.loadingCaseResult = false
      }, response => {

      })
    },

    changePage: function (value) {
      this.$axios.get('/api/ci/auto_case_results?TaskResultID='+ this.testResultID +'&page='+value).then(response => {
        this.caseResults = response.data.result
      }, response => {

      })
    },

    filterCaseResult: function (value) {
      this.getCaseResults(this.testResultID,value)
    },

    exportTestResult: function () {
      let realUrl = '/api/ci/task/testresult/export_case_result/' + this.testResultID
      this.exporting = true
      this.$axios({url: realUrl, method: 'get', responseType: 'arraybuffer'}).then(response => {
        this.exporting = false
        let url = window.URL.createObjectURL(new Blob([response.data], {type: 'application/vnd.ms-excel'}))
        let link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.setAttribute('download', this.testResultID + '.xls')
        document.body.appendChild(link)
        link.click()
        link.remove()
      }, response => {
      })
    }

  },

  created: function () {
    this.caseResults = []
    this.getTestResult(this.testResultID)
    this.getCaseResults(this.testResultID,0)
  },

  mounted: function () {
  },

  watch: {
    testResultID: function (value) {
      this.caseResults = []
      this.getTestResult(value)
      this.getCaseResults(value,0)
    }
  },

  components: {
    ISwitch,
    ICol,
    labelEditorInput
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">

  .member-card {
    width: 100%;
    margin-bottom: 10px;
  }
  .summary-number-title{
    text-align: left;
    margin-left: -10px;
    margin-top: -18px;
    font-size: 18px;
  }

  .summary-number-content{
    text-align: center;
    font-size: 30px;
    /*text-decoration: underline;*/
  }

  .content{
    padding-left: 5px;
  }

</style>



